@import url(https://fonts.googleapis.com/css2?family=Lato&family=Open+Sans:wght@300&family=Oswald:wght@300&family=Roboto+Condensed&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-colorOne: #2196f3;
  --main-colorTwo: #21cbf3;

  --small-fontOne: 0.375em;
  --small-fontTwo: 0.625em;
  --small-fontThree: 0.875em;

  --medium-fontOne: 1em;
  --medium-fontTwo: 1.375em;
  --medium-fontThree: 1.75em;

  --large-fontOne: 2.5em;
  --large-fontTwo: 3.5em;
  --large-fontThree: 4rem;

  --fontStyle-One: "Roboto", sans-serif;
  --fontStyle-Two: "Roboto Condensed", sans-serif;
  --fontStyle-Three: "Oswald", sans-serif;
  --fontStyle-Four: "Open Sans", sans-serif;
  --fontStyle-Five: "Lato", sans-serif;
}

.colorTextOne {
  color: #2196f3;
}
.colorTextTwo {
  color: #21cbf3;
}

.muiavatar {
  background: slategray;
}

.btnFill {
  padding: 8px 35px;
  outline-width: 0;
  cursor: pointer;
  border: none;
  text-align: center;
  letter-spacing: 0.2rem;
  border-radius: 5px;
  background-color: #21cbf3;
  background-color: var(--main-colorTwo);
  color: #ffffff;
  font-size: 0.875em;
  font-size: var(--small-fontThree);
  font-family: "Lato", sans-serif;
  font-family: var(--fontStyle-Five);
}

.btnOutline {
  padding: 2px 7px;
  outline-width: 0;
  border: none;
  border-radius: 5px;
  border: 2px solid #2196f3;
  border: 2px solid var(--main-colorOne);
  color: #21cbf3;
  color: var(--main-colorTwo);
  font-size: 0.875em;
  font-size: var(--small-fontThree);
  font-family: "Lato", sans-serif;
  font-family: var(--fontStyle-Five);
}

.center {
  text-align: center;
}

.link {
  text-decoration: none;
  color: #21cbf3;
  color: var(--main-colorTwo);
  opacity: 0.7;
}

.link:hover {
  opacity: 1;
}

.h1 {
  background: linear-gradient(45deg, #2196f3 30%, #21cbf3 90%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.h2 {
  color: #21cbf3;
  color: var(--main-colorTwo);
}

.title {
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.title::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 40%;
  height: 3px;
  background: navy;
  width: 0%;
}

.title:hover::after {
  width: 20%;
  transition: all 0.5s ease-in-out;
  cursor: grabbing;
}

.svgIcon {
  color: #fff;
}

.rowDis {
  display: flex;
  align-items: center;
}

.colDis {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.divCenter {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.box {
  border-radius: 50px;
  background: linear-gradient(145deg, #ffffff, #d9d8e6);
  box-shadow: 5px 5px 10px #898991, -5px -5px 10px #ffffff;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 2rem;
  border-top: 5px solid #2196f3;
  border-top: 5px solid var(--main-colorOne);
}

.primary {
  background: #2196f3;
  color: #fff;
  transition: all 1s;
  text-transform: capitalize;
  box-shadow: 0 3px 5px 2px rgba(255, 105, 135, 0.3);
}

.primary:hover {
  background: linear-gradient(45deg, #2196f3 30%, #21cbf3 90%);
}

.hide {
  display: none;
}

.m1 {
  margin: 10px;
}

.m2 {
  margin: 20px;
}

.m3 {
  margin: 30px;
}
.mt1 {
  margin-top: 10px;
}

.mt2 {
  margin-top: 20px;
}

.mt3 {
  margin-top: 30px;
}
.mt4 {
  margin-top: 40px;
}
.mt5 {
  margin-top: 50px;
}

.mb1 {
  margin-bottom: 10px;
}

.mb2 {
  margin-bottom: 20px;
}

.mb3 {
  margin-bottom: 30px;
}
.mb4 {
  margin-bottom: 40px;
}
.mb5 {
  margin-bottom: 50px;
}

.p1 {
  padding: 10px;
}

.p2 {
  padding: 20px;
}

.p3 {
  padding: 30px;
}
.pt1 {
  padding-top: 10px;
}

.pt2 {
  padding-top: 20px;
}

.pt3 {
  padding-top: 30px;
}

.pb1 {
  padding-bottom: 10px;
}

.pb2 {
  padding-bottom: 20px;
}

.pb3 {
  padding-bottom: 30px;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
  background: linear-gradient(45deg, #2196f3 30%, #21cbf3 90%);
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  color: #fff;
  box-shadow: 5px 5px 10px #cccccc, -5px -5px 10px #f4f4f4;
  padding: 0px 10px;
}

.headerLeft {
  display: flex;
  align-items: center;
}

.headerLeft .logo {
  margin-right: 15px;
}

.header .logo a {
  color: #fff;
}

.headerRight .badge button {
  margin-right: 10px;
  background: transparent;
  color: #fff;
  transition: all 0.5s ease;
  text-transform: capitalize;
  border: 1px solid white;
}

/* .headerRight button:hover {
  background: linear-gradient(45deg, #2196f3 30%, #21cbf3 90%);
} */

.headerMenu {
  display: flex;
  align-items: center;
}

.mobile {
  display: none;
}

.cart {
  padding-left: 10px;
  /* color: #fff; */
  position: relative;
}

.cart:hover .HoverContainer {
  display: flex;
  flex-direction: column;
}

.HoverContainer {
  display: none;
  position: absolute;
  right: 5px;
  top: 27px;
  background: #fff;
  width: 300px;
  height: 500px;
  padding: 10px;
  color: #000;
  border: 1px solid lightgray;
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}

.squareBox,
.squareBoxleft {
  width: 25px;
  height: 25px;
  background: #fff;
  transform: rotate(45deg);
  position: absolute;
  right: 20px;
  top: -11px;
  z-index: 99;
}

.squareBoxleft {
  left: 20px;
  width: 15px;
  height: 15px;
  top: -5px;
}

.HoverContainer button {
  color: #00bcd5;
}

.cartHoverItem p {
  font-size: 14px;
}

.cartHoverItem .quantity {
  margin: 10px 0px;
}

.cartHoverItem .quantity input {
  width: 40px;
}


.cartHoverItem .quantityBox {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.cartHoverItem .quantity p {
  margin: 0 5px;
}

.cartHoverItem .quantity button {
  margin: 0px 5px;
  width: 15px;
  height: 15px;
  border-radius: 50px;
  outline-width: 0;
  background: gray;
  border: none;
  color: #fff;
  cursor: pointer;
}

.cartItemImage {
  position: relative;
  margin: 10px;
}

.cartRemove {
  position: absolute;
  right: 0;
  top: 0px;
  width: 20px;
  height: 20px;
  border-radius: 50px;
  color: black;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  filter: drop-shadow(1px 1px 5px gray);
  z-index: 99;
  cursor: pointer;
}

.ellipsis {
  /* white-space: nowrap; */
  width: 100%;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}

.cartRemove .MuiSvgIcon-root {
  width: 15px;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .mobile {
    display: flex;
  }

  .headerMenu {
    display: none;
  }

  .HoverContainer {
    left: -116px;
    top: 50px;
    border: 2px solid lightgray;
  }
}

.main {
  display: flex;
  align-items: flex-start;
  flex-grow: 1;
  width: 100%;
}

.sidebar {
  flex: 0.25 1;
  background: #f7f7fa;
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 60px;
  z-index: 1;
  height: 90vh;
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar a {
  text-decoration: none;
  list-style: none;
  color: #000;
}

.products {
  flex: 1 1;
  padding: 10px;
}
.productsContainer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.link {
  color: #2196f3;
  background-color: #f2f2f2;
}

.productCard {
  width: 14vmax;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 2vmax;
  transition: all 0.5s;
  padding-bottom: 0.5vmax;
  height: 300px;
  position: relative;
}

.chip {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.productCard > img {
  max-height: 14vmax;
  height: 100%;
}

.productCard > div {
  margin: 0.5vmax;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.productCardSpan {
  margin: 0.5vmax;
  font: 300 0.7vmax "Roboto";
}

.productCard > p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1vmax;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);

  transform: translateY(-1vmax);
}

.productModal {
  display: flex;
  align-items: flex-start;
  /* justify-content: space-around; */
  width: 100%;
}

.productModal_Img {
  margin-right: 20px;
  max-width: 20vmax;
}

.productModal_Img img {
  width: 100%;
  height: 22vmax;
  object-fit: contain;
}
.productModal_Details h2 {
  margin: 8px 0px;
}

.quantity p {
  margin: 0px 10px;
}

.quantity button {
  margin: 10px 0px;
}

.productModal_Details .cart_fav_Btn {
  max-width: 300px;
  width: 100%;
  justify-content: space-between;
}

.productModal_Details .cart_fav_Btn button,
.productModal_Details .buyNow_Btn {
  width: 100%;
  margin-right: 8px;
  margin-top: 8px;
  background: #2196f3;
  color: white;
  border-radius: 30px;
}

.productModal_Details .buyNow_Btn {
  max-width: 300px;
  width: 100%;
}

.productModal_Details .cart_fav_Btn button {
  background: white;
  color: #2196f3;
}

@media screen and (max-width: 600px) {
  .sidebar {
    display: none;
  }

  .products {
    flex: 1 1;
    width: 100%;
  }

  .productCard {
    max-width: 22vmax;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 200px;
  }

  .productCard > img {
    max-height: 20vmax;
    height: 100%;
  }

  .productModal {
    flex-direction: column;
    align-items: center;
  }

  .productModal_Details .cart_fav_Btn button {
    margin: 5px;
  }

  .productModal_Img {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .productModal_Img img {
    width: 80%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    object-fit: contain;
  }

  .productCard > p {
    font-size: 1.7vmax;
  }

  .productCard > div {
    margin: 0vmax;
    display: block;
  }

  .productCard > span {
    font-size: 1.5vmax;
  }

  .productCard > div > span {
    margin: 0 0.5vmax;
    font: 300 1vmax "Roboto";
  }
}

